<template>
  <div class="backgroundArea" :style="{ backgroundImage: 'url('+ backgroundImg +')'}">
    <div class="header">
        <span @click="back" class="back">
            <img src="../../assets/img/back.png" class="releaseIcon">
        </span>
        <span class="usersTitle"></span>
        <span class="back"></span>
    </div>
    <form class="enroll">
         <div class="accountAndpwd">
            <div class="account">
                <label>
                    <span>手机号</span>
                </label>
                <input type="tel" placeholder="请输入11位手机号码" maxlength="11" v-model="enrollForm.account">
                <span class="clear" @click="clearAccount">
                    <img src="../../assets/img/clear.png">
                </span>
            </div>
            <div class="verificationCode">
                <span>
                    <input type="tel" placeholder="请输入您的验证码" maxlength="6" v-model="enrollForm.verificationCode" autocomplete="tel">
                </span>
                <span style="border: none;">
                    <button type="button" class="getCode" :class="{ disabled: !canClick }" @click="getCode">{{ content }}</button>
                </span>
            </div>
            <div class="pwd">
                <label>
                    <span>密&ensp;码</span>
                </label>
                <input type="password" placeholder="输入不少于6位的字母、数字组合" v-model="enrollForm.password" autocomplete="new-password">
                <span class="clear" @click="clearPwd">
                    <img src="../../assets/img/clear.png">
                </span>
            </div>
            <div class="inviteCode">
                <label>
                    <span>邀请码</span>
                </label>
                <input type="text" placeholder="请输入邀请码（选填）" v-model="enrollForm.inviteCode" autocomplete="text">
                <span class="clear" @click="clearInviteCode">
                    <img src="../../assets/img/clear.png">
                </span>
            </div>
            <span class="notice">
                <input type="checkbox" class="agreement">
                <span class="agreementNote">注册即代表哼哈运动用户协议及隐私政策</span>
            </span>
            <button type="button" @click="enroll" class="nextStep">注册</button>
        </div>
    </form>
  </div>
</template>

<script>
import { getCode, checkAccount, checkCode } from '../../api/getdata'
// import { setCookie } from '../../../static/js/cookieUtil'
import { Toast } from 'mint-ui'
export default {
  name: 'enroll',
  data () {
    return {
      backgroundImg: require('../../assets/img/login.png'),
      pwdType: 'password',
      content: '获取验证码',
      totalTime: 60,
      canClick: true,
      enrollForm: {
        account: '',
        verificationCode: '',
        password: '',
        inviteCode: ''
      }
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    showPwd () {
      this.pwdType = this.pwdType === 'password' ? 'text' : 'password'
    },
    getCode () {
      this.enrollForm.verificationCode = ''
      if (this.enrollForm.account) {
        let data = checkAccount({fMobile: this.enrollForm.account})
        data.then(res => {
          if (res.status === '-1') {
            Toast({
              message: '该手机号已被注册',
              duration: 1500
            })
          } else {
            Toast({
              message: '验证码已发送',
              duration: 1500
            })
            let data = getCode({fMobile: this.enrollForm.account})
            data.then(res => {
              console.log(res)
            })
              .catch(err => {
                console.log(err)
              })
            if (!this.canClick) {
              return
            }
            this.canClick = false
            this.content = '获取验证码(' + this.totalTime + 's)'
            let clock = window.setInterval(() => {
              this.totalTime--
              this.content = '获取验证码(' + this.totalTime + 's)'
              if (this.totalTime < 0) {
                window.clearInterval(clock)
                this.content = '重新获取验证码'
                this.totalTime = 60
                this.canClick = true
              }
            }, 1000)
          }
        })
      } else {
        Toast({
          message: '请输入手机号',
          duration: 1500
        })
      }
    },
    enroll () {
      // console.log(this.enrollForm)
      if (this.enrollForm.account && this.enrollForm.verificationCode && this.enrollForm.password) {
        let data = checkCode({
          fMobile: this.enrollForm.account, code: this.enrollForm.verificationCode
        })
        data.then(res => {
          console.log(res)
          if (res.status === '0') {
            Toast({
              message: '验证成功',
              duration: 2000
            })
            localStorage.setItem('registeAccount', this.enrollForm.account)
            localStorage.setItem('registeVerificationCode', this.enrollForm.verificationCode)
            localStorage.setItem('registePassword', this.enrollForm.password)
            localStorage.setItem('inviteCode', this.enrollForm.inviteCode)
            setTimeout(() => {
              this.$router.push('/home/enroll/identify')
            }, 1000)
          } else if (res.data.status === '-1') {
            Toast({
              message: '输入有误，请重新输入',
              duration: 2000
            })
          }
        })
      } else {
        // console.log('false')
        Toast({
          message: '输入内容不能为空',
          duration: 2000
        })
      }
    },
    clearAccount () {
      this.enrollForm.account = ''
    },
    clearPwd () {
      this.enrollForm.password = ''
    },
    clearInviteCode () {
      this.enrollForm.inviteCode = ''
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.426667rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .enroll {
    width: calc(100% - 1.22667rem);
    margin: 0 auto;
    margin-top: 4.5rem;
  }

  .backgroundArea {
    width: 100%;
    height: auto;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
  }

  .accountAndpwd {
    width: 100%;
  }

  .accountAndpwd label{
    width: 2.2rem;
    justify-content: center;
    display: flex;
    align-items: center;
    color: #ffd200;
  }

  .accountAndpwd label span {
    vertical-align: middle;
    font-size: 0.42667rem;
  }

  .inviteCode input,
  .account input,
  .pwd input {
    background: transparent;
    border: none;
    outline: none;
    height: 1.33333rem;
    flex-grow: 1;
    font-size: 0.42667rem;
  }

  .accountAndpwd button {
    background: #ffd200;
    outline: none;
    border: none;
  }

  .inviteCode,
  .accountAndpwd button,
  .account,
  .pwd {
    width: 100%;
    height: 1.33333rem;
  }

  .accountAndpwd button {
    border-radius: 0.15rem;
  }

  .verificationCode {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.6667rem;
  }

  .verificationCode input {
    height: 1.3333rem;
    background: transparent;
    border: none;
    outline: none;
    color: #333333;
    padding: 0 0.6rem 0 0.4rem;
  }

  .verificationCode span {
    display: block;
    width: 4.266667rem;
    border-bottom: 1px solid #ffd200;
  }

  .disabled {
    background: #ddd!important;
  }

  .inviteCode,
  .account,
  .pwd {
    display: flex;
    margin: 0.6667rem auto;
    border-bottom: 1px solid #ffd200;
    align-items: center;
  }

  .inviteCode {
    margin-bottom: 0;
  }

  .pwd {
    justify-content: space-between;
  }

  .nextStep {
    margin-top: 1.06667rem;
    color: #fff;
    font-size: 0.48rem;
  }

  .notice {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0.45333rem;
    color: #666666;
    font-size: 0.4rem;
  }

  .getCode {
    color: #fff;
    font-size: 0.42667rem;
  }

  .agreement {
    width: 0.6rem;
    height: 0.6rem;
    border: 1px solid #ffd200;
    border-radius: 0.15rem;
  }

  .agreementNote {
    margin-left: 0.26667rem;
    font-size: 0.34667rem;
    color: #666666;
  }

  .clear {
    display: flex;
    width: 0.56rem;
    height: 0.56rem;
    align-items: center;
    justify-content: center;
  }

  input::placeholder {
    color: #c0c0c0;
    font-size: 0.4rem;
  }
</style>



